<template>
    <div class="head-box">
    	<el-row :gutter="24">
		  <el-col :lg="12" :sm="12" :xs="12"><div class="grid-content bg-purple nowrap-line">欢迎 <strong>{{this.$store.state.userName}}</strong> 登陆 <strong>xzxc</strong> 执行系统</div></el-col>
		  <el-col :lg="12" :sm="12" :xs="12"><div class="grid-content bg-purple" style="text-align:right;cursor:pointer;" @click="logout"><strong>退出</strong></div></el-col>
		</el-row>
    </div>
</template>

<script>
  export default {
  	data () {
  		return {
  			userName: ''
  		}
  	},
  	created () {
  	},
  	methods: {
  		logout () {
  			var _this = this
			_this.$get({
				type: '$get',
				obj: {
				  url: _this.api.logout,
				  success: function (res) {
				    if (res.status == 200) {
				      window.location.reload()
				    }
				    else {
				      _this.alerter('退出登陆失败！','退出',2000)
				    }
				  },
				  error: function () {
				    _this.alerter('退出登陆失败！','退出',2000)
				  }
				},
				param: {
				  id: 1
				}
			})
  		}
  	}
  }
</script>
<style>
.head-box{line-height:60px;color:#fff;}
.nowrap-line{white-space:nowrap;overflow:hidden;text-overflow:ellipsis;}
</style>


