import Vue from 'vue'
import Router from 'vue-router'
import routePage from './routePage/routePage'
const index = r => require.ensure([], () => r(require('@/components/index')), 'index')
const create_menu = r => require.ensure([], () => r(require('@/components/menu/create_menu')), 'create_menu')
const create_user = r => require.ensure([], () => r(require('@/components/user/index')), 'create_user')
const user_list = r => require.ensure([], () => r(require('@/components/user/user')), 'user_list')
const imglibs = r => require.ensure([], () => r(require('@/components/imgLibs')), 'imglibs')
const imglibslist = r => require.ensure([], () => r(require('@/components/imglibslist')), 'imglibslist')
Vue.use(Router)
const routeArr = [
  {
      path: '/',
      name: 'index',
      component: index,
      meta: {
        name: '主页',
        title: [
          {
            name: '主面板',
            path: '/'
          }
        ]
      }
  },
  {
    path: '/create_menu',
    name: 'create_menu',
    component: create_menu,
    meta: {
      name: '菜单管理',
      title: [
        {
          name: '菜单管理',
          path: null
        }
      ]
    }
  },
  {
    path: '/user_list',
    name: 'user_list',
    component: user_list,
    meta: {
      name: '用户数据',
      title: [
        {
          name: '用户管理',
          path: '/create_user'
        },
        {
          name: '用户数据',
          path: '/user_list'
        }
      ]
    }
  },
  {
    path: '/create_user',
    name: 'create_user',
    component: create_user,
    meta: {
      name: '创建用户',
      title: [
        {
          name: '用户管理',
          path: '/create_user'
        },
        {
          name: '创建用户',
          path: '/create_user'
        }
      ]
    }
  },
  {
    path: '/imglibs',
    name: 'imglibs',
    component: imglibs,
    meta: {
      name: '图库管理',
      title: [
        {
            name: '主面板',
            path: '/'
        },
        {
          name: '图库管理',
          path: '/imglibs'
        }
      ]
    }
  },
  {
      path: '/imglibslist',
      name: 'imglibslist',
      component: imglibslist,
      meta: {
        name: '图库数据',
        title: [
          {
              name: '主面板',
              path: '/'
          },
          {
            name: '图库数据',
            path: '/imglibslist'
          }
        ]
      }
    }
]
const routeArrs = routeArr.concat(routePage)
export default new Router({
  routes: routeArrs
})



