<template>
  <div class="xzxc-menu" :span="12">
    <div class="xzxc-menu-toggle" @click="menuToggle()"><i class="el-icon-menu"></i></div>
    <el-scrollbar :style="'height:'+h+'px;'">
      <el-menu class="menu-Bar el-menu-vertical-demo" router :default-active="$route.path" :collapse-transition="true" :collapse="this.$store.state.menuLayout" background-color="#545c64" text-color="#fff" active-text-color="#ffd04b">
          <template v-for="item in menuData">
            <sub-menu :param="item"></sub-menu>
          </template>
        </el-menu>
    </el-scrollbar>
  </div>
</template>

<script>
import menudatas from './menu.js'
import subMenu from './menutree'
import {mapState, mapMutations} from 'vuex'
export default {
  components: {
    subMenu
  },
  beforeUpdate:function(to,from){
    console.log(to,from)
  },
  name: 'HelloWorld',
  data () {
    return {
  		isCollapse: false,
  		activeIndex: '/',
      h:0,
      menuData: null,
      menuTreeData:{
        name:'menu_name',
        href:'menu_href',
        icon:'menu_icon',
        parentId:'cid',
        hrefP: 'menu_href'
      }
    }
  },
  computed: {
    ...mapState({
      api: 'api',
      getCatch: 'getCatch',
      setCatch: 'setCatch',
      alertsu: 'alertsu',
      alerter: 'alerter'
    }),
    ...mapState('createTree',{
      tree1: state => state.tree1,
      data2treeDG: state => state.data2treeDG
    })
  },
  props: ['hei'],
  created () {
    this.defaultIdex()
    this.getMenu()
    this.h = this.hei - 30
  },
  methods: {
    ...mapMutations({
      $axios: '$axios',
      $get: '$get'
    }),
    getMenu () {
      var _this = this
      _this.$get({
        type: '$get',
        obj: {
          url: _this.api.getusermenu,
          success: function (res) {
            if (res.status == 200) {
              _this.$store.state.userInfo = res.data
              _this.menuData = JSON.parse(res.data.user_menu_jurisdiction)
              _this.$store.state.userName = res.data.user_name
            }
            else {
              _this.alerter('获取菜单数据失败！','获取菜单数据',2000)
            }
          },
          error: function () {
            _this.alerter('获取菜单数据失败！','获取菜单数据',2000)
          }
        },
        param: {
          id: 1
        }
      })
    },
    menuToggle () {
      this.$store.state.menuLayout = !this.$store.state.menuLayout
    },
    defaultIdex(){
        let path = this.$route.path;
        // console.log(this.$route.matched[1].path);
    }
  }
}
</script>
<style lang="less">
.xzxc-menu-toggle{
  height:30px;background-color:#4a5064;text-align:center;line-height:30px;cursor:pointer;
}
.el-menu{border:0;}
.el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 200px;
  }
  .el-menu--collapse>.el-menu-item span, .el-menu--collapse>.el-submenu>.el-submenu__title span{
  height: 0;
    width: 0;
    overflow: hidden;
    visibility: hidden;
    display: inline-block;
  }
.el-scrollbar__wrap {
  overflow-x: hidden;
}
</style>



