<template>
  <div id="app" v-loading.fullscreen.lock="loading">
    <el-container class="xzxc-app-layout-box">
      <el-header class="xzxc-app-layout-header"><head-box></head-box></el-header>
      <el-container class="xzxc-app-layout-body" ref="xzxcConBody">
          <el-aside class="xzxc-app-layout-left" :width="menuw">
            <menu-cd @setMenuToggleCallback="setMenuToggle" v-if="asideh" :hei="asideh"></menu-cd>
          </el-aside>
          <el-main class="xzxc-app-layout-main">
            <el-breadcrumb separator="/" style="padding:20px 0px 17px 20px;border-bottom:1px solid #dedbdb;">
              <el-breadcrumb-item v-if="doctitle" v-for="item in doctitle" :key="item.name" :to="item.path">{{item.name}}</el-breadcrumb-item>
            </el-breadcrumb>
            <el-scrollbar :style="'height:'+scrollH+'px;padding:0px 0px 0px 20px;'">
              <router-view/>
            </el-scrollbar>
          </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import menuCd from './components/menu/index'
import headBox from './components/head/index'
import { mapState } from 'vuex'
export default {
  name: 'App',
  components: {
    menuCd,
    headBox
  },
  data () {
    return {
      menuw: 'auto',
      menushow: true,
      asideh: null,
      scrollH: 0,
      editableTabsValue2: '/'
    }
  },
  created () {
    this.editableTabsValue2 = window.location.hash.replace('#','')
  },
  mounted () {
    var _this = this
    _this.$store.state.setMainHeight = function () {
      _this.asideh = _this.$refs.xzxcConBody.$el.clientHeight
      _this.scrollH = _this.asideh - 55
    }
    _this.$store.state.setMainHeight()
  },
  computed: {
    ...mapState({
      loading: 'loading',
      doctitle: 'doctitle',
      tabs: 'tabs'
    })
  },
  methods: {
    removeTab (){

    },
    setMenuToggle () {
      this.menushow = !this.menushow
      if (this.menushow) {
        this.menuw = '200px'
      } else {
        this.menuw = '50px'
      }
    }
  }
}
</script>

<style lang="less">
hr{
  background-color:#dedede;height:1px;border:0;
  display:block;clear:both;width:100%;margin-bottom:20px;
}
html,body,#app{
  width:100%;height:100%;
}
*{
  margin:0px;
  padding:0px;
}
.xzxc-app,.xzxc-app-layout-box,.xzxc-app-layout-left{
  width:100%;height:100%;
}
.xzxc-app-layout-left,.xzxc-app-layout-body{
  overflow:hidden;
}
.xzxc-app-layout-header,.xzxc-app-layout-body{
  background-color:#373d41;
}
.xzxc-app-layout-main{
  background-color:#fff;overflow:hidden;padding:0px;
}
.xzxc-app-layout-main .el-scrollbar__view{
  padding-right:20PX;
}
.fa{
  margin-right:5px;font-size:18px;width:24px;text-align:center;
}
.el-select-dropdown__list{
      overflow: scroll;
}
.el-pager li{
      background-color:#eceaea;
    margin-right: 10px;border-radius:3px;
    color:#6b6f71;
}
.el-pager li.active {
    color: #409EFF;
    cursor: default;
    background: #409EFF;
    color: #fff;
}
.is-ready{display:none!important;}
</style>



