// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import App from './App'
import router from './router'
import { store } from './store/index'
import beforeEachFn from './router/beforeEach.js'
import axios from 'axios'
import VueCookies from 'vue-cookies'
import xzxcPlugin from './plugin/index.js'
const qs = require('qs');
import VueQuillEditor from 'vue-quill-editor'

// require styles
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'

Vue.use(VueQuillEditor, /* { default global options } */)
import "./assets/css/font-awesome.min.css"
Vue.use(VueCookies)
Vue.use(ElementUI)
Vue.use(xzxcPlugin)
Vue.config.productionTip = false
/* eslint-disable no-new */
const _vm_ = new Vue({
  el: '#app',
  store,
  router,
  components: { App },
  template: '<App/>'
})
store.commit({
	type: 'setvue',
	vm: _vm_
})
store.commit({
  type: 'setstore',
  store: store
})
store.commit({
	type: '$ajax',
	axios: axios,
	store: store
})
store.commit({
  type: 'api'
})
store.commit({
  type: 'qs',
  qs: qs
})
store.commit({
  type: 'alert',
  vm: _vm_
})
store.commit({
  type: 'windowresize'
})
router.beforeEach(function (to, from, next) {
  document.title = to.meta.title[to.meta.title.length - 1].name
  beforeEachFn.showLoading(store)
  beforeEachFn.setDocTitle(store, to.meta.title)
  // store.commit({
  //   type: 'addTabs',
  //   name: to.path,
  //   title: to.meta.name
  // })
  next()
})
router.afterEach(function (to, from, next) {
  document.title = to.meta.title[to.meta.title.length - 1].name
  beforeEachFn.hideLoading(store)
  beforeEachFn.setDocTitle(store, to.meta.title)
})


