const emotion = r => require.ensure([], () => r(require('@/components/emotion/index')), 'emotion')
const emotionList = r => require.ensure([], () => r(require('@/components/emotion/list')), 'emotionList')
const themeIndex = r => require.ensure([], () => r(require('@/components/theme/index')), 'themeIndex')
const themeList = r => require.ensure([], () => r(require('@/components/theme/list')), 'themeList')
const routePage = [
	{
      path: '/emotion',
      name: 'emotion',
      component: emotion,
      meta: {
        name: '情感管理发布',
        title: [
        	{
        		name: '情感管理',
        		path: '/'
        	},
        	{
        		name: '情感管理发布',
        		path: '/emotion'
        	}
        ]
      }
    },
    {
      path: '/emotion/:type',
      name: 'emotion',
      component: emotion,
      meta: {
        name: '情感管理编辑',
        title: [
        	{
        		name: '情感管理',
        		path: '/'
        	},
        	{
        		name: '情感管理编辑',
        		path: null
        	}
        ]
      }
    },
    {
      path: '/et/list',
      name: 'emotionList',
      component: emotionList,
      meta: {
        name: '情感管理数据一览',
        title: [
        	{
        		name: '情感管理',
        		path: '/'
        	},
        	{
        		name: '情感管理数据一览',
        		path: '/et/list'
        	}
        ]
      }
    },
    {
      path: '/theme/create',
      name: 'themeIndex',
      component: themeIndex,
      meta: {
        name: '情感管理主题创建',
      	title: [
        	{
        		name: '情感管理',
        		path: '/et/list'
        	},
        	{
        		name: '情感管理主题创建',
        		path: '/theme'
        	}
        ]
      }
    },
    {
      path: '/theme/list',
      name: 'themeList',
      component: themeList,
      meta: {
        name: '情感主题数据',
        title: [
          {
            name: '情感管理',
            path: '/et/list'
          },
          {
            name: '情感主题数据',
            path: '/theme'
          }
        ]
      }
    }
]
export default routePage


