<template>
    <el-submenu :index="item.href || child.hrefP" v-if="item.children && item.children.length>0" class="el-menu-sub">
	    <template slot="title"><i :class="item.icon"></i><span slot="title">{{item.name}}</span></template>
	    <template v-for="child in item.children">
	      <sub-menu v-if="child.children && child.children.length>0" :param="child"></sub-menu>
	      <el-menu-item :index="child.href || child.hrefP" v-else><i :class="child.icon"></i><span slot="title">{{child.name}}</span></el-menu-item>
	    </template>
	  </el-submenu>
    <el-menu-item :index="item.href || child.hrefP" v-else class="el-menu-each"><i :class="item.icon"></i><span slot="title">{{item.name}}</span></el-menu-item>
</template>

<script>
  import subMenu from './menutree'
  export default {
    name: 'subMenu',
    props: ['param'],
    data: function () {
      return {item: this.param}
    },
    components: {
      subMenu
    }
  }
</script>


